
.scrollbar_style{
  &::-webkit-scrollbar {
      width: 8px;
      height: 8px;
  }
  &::-webkit-scrollbar-thumb {
      min-height: 28px;
      border-radius: 4px;
      background-color:#cccccc;
            
  }
  &::-webkit-scrollbar-track-piece,&::-webkit-scrollbar-corner {
      background-color: transparent;
  }
}
.idmcore_document{
    .idm_top_toolbar{
        position: relative;
        height: 60px;
        line-height: 60px;
        background-color:#FFFFFF;
        box-shadow: 0px 3px 5px rgba(0,0,0,0.08);
        z-index: 2;
        display: flex;
        justify-content: space-between;
        .idm_top_toolbar_left{
            padding-left: 20px;
            cursor: pointer;
            .switch-icon{
                font-size: 24px;
                color: #40a9ff;
            }
            .checked_codepackage_name{
                font-size: 18px;
                margin-left: 10px;
                font-weight: bold;
            }
            .checked_codepackage_version{
                font-size: 14px;
                margin-left: 10px;
                margin-right: 10px;
            }
            .ant_icon_down{
                color: #CCC;
            }
        }
        .idm_top_toolbar_right{
            display: flex;
            justify-content: flex-end;
            & > div {
              padding: 0px 8px;
              white-space: nowrap;
              cursor: pointer;
              > a {
                display: block;
              }
              &[disabled] {
                opacity: 0.3;
                cursor: not-allowed;
                a {
                  cursor: not-allowed;
                }
                .savebtn {
                  &:hover {
                    opacity: 1;
                  }
                }
              }
              svg {
                outline: none;
              }
            }
            .oneicon {
              font-size: 16px;
            }
            .savebtn {
              padding: 0px;
              margin-left: 8px;
              width: 80px;
              text-align: center;
              font-size: 16px;
              background-color: #1890ff;
              color: #ffffff;
              transition: all linear 0.3s;
              &:hover {
                opacity: 0.8;
              }
            }
        }
    }
    .idm_content_box{
        display: flex;
        flex-wrap: nowrap;
        .idm_cb_module_left{
            width: 40px;
            flex-grow: 0;
            background: #FFF;
            position: relative;
            border-right: 1px solid #DDDDDD;
            .idm_cb_module_left_title{
              display: flex;
              justify-content: space-between;
              position: relative;
              color: #2c3e50;
              font-weight: 600;
              font-size: 16px;
              align-items: center;
              span{
                display: block;
                cursor: pointer;
                width: 100%;
                text-align: center;
                padding: 10px;
                background-color:#F7F7F7;
                &:hover{
                  background-color:#E6F7FF ;
                }
              }
              i{
                color: #999;
                cursor: pointer;
                display: none;
              }
              &:after {
                  position: absolute;
                  right: 0;
                  bottom: 0;
                  left: 0;
                  border-bottom: 1px solid #ddd;
                  content: "";
              }
            }
            .idm_cb_module_left_search{
              padding: 15px;
              display: none;
            }
            .idm_cb_module_left_list{
              height: calc(100vh - 163px);
              overflow: auto;
              display: none;
              >div{
                padding:15px;
                cursor: pointer;
                transition: all linear 0.3s;
                &.index_doc{
                  border-bottom: 1px solid #F1F1F1;
                  border-top: 1px solid #F1F1F1;
                }
                .item_title{
                  font-size: 16px;
                  display: flex;
                  justify-content: space-between;
                  *{
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                  }
                  label{
                    color: #999999;
                  }
                  span{
                    color: #333333;
                  }
                }
                .item_desc{
                  text-overflow: ellipsis;
                  overflow: hidden;
                  white-space: nowrap;
                  margin-top: 5px;
                  font-size: 14px;
                  color: #40a9ff;
                }
                &:hover,&.active{
                  background-color: #E6F7FF;
                }
              }
            }
        }
        .idm_cb_module_editor{
            flex-grow: 2;
        }
        &.show_left{
          .idm_cb_module_left{
            width: 300px;
            .idm_cb_module_left_title{
              height: 41px;
              line-height: 41px;
              white-space: nowrap;
              padding: 0px 14px;
              i{
                display: block;
              }
              span{
                width: auto;
                text-align: left;
                padding: 0px;
                background-color:transparent ;
                &:hover{
                  background-color:transparent ;
                }
              }
            }
          }
          .idm_cb_module_left_search,.idm_cb_module_left_list{
            display: block;
          }
        }
    }
}
.idm_popover_codepackage_choose_outbox .idm-ant-popover-inner-content{
  padding: 0;
}
.idm_popover_codepackage_choose{
    height: 300px;
    display: flex;
    flex-wrap: nowrap;
    ul{
      list-style: none;
      padding: 0;
      margin: 0;
      &:first-child{
        border-right: 1px solid #F1F1F1;
      }
      li{
        transition: all linear 0.3s;
        padding: 0px 15px;
        cursor: pointer;
        label{
          transition: all linear 0.3s;
        }
        &:hover,&.active{
          background-color: #E6F7FF;
          color: #40a9ff;
        }
      }
    }
    .idm_codepackage_list_box{
      width: 220px;
      height: 100%;
      overflow-y: auto;
      li{
        height: 36px;
        line-height: 36px;
        display: flex;
        justify-content: space-between;
        span{
          font-weight: bold;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          padding-right: 20px;
        }
        label{
          color: #40a9ff;
        }
      }
    }
    .idm_codepackage_version_box{
      width: 250px;
      height: 100%;
      overflow-y: auto;
      li{
        .version_title_box{
          padding-top: 10px;
          padding-bottom: 5px;
          // height: 36px;
          // line-height: 36px;
          display: flex;
          justify-content: space-between;
          span{
            font-weight: bold;
          }
          label{
            color: #999999;
          }
        }
        .version_desc{
          color: #999999;
          padding-bottom: 15px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }
      }
    }
}